<template>
	<view class="grid justify-around action-wrapper">
		<view class="grid col-1">
			<button id="btn-my-avatar" 
					class="cu-btn round action-btn bg-yellow shadow " 
					open-type="getUserInfo" 
					@getuserinfo="getUserInfo" 
					:class="btnColor">我的头像</button>
		</view>
		<view class="grid col-2">
			<button id="btn-save" 
					class="cu-btn round action-btn bg-yellow shadow" 
					@click="handleSave" 
					:class="btnColor">保存头像</button>
		</view>
		<view class="grid col-3"> 
			<button id="btn-save" 
					class="cu-btn round action-btn bg-yellow shadow" 
					open-type="share" 
					:class="btnColor">分享朋友</button>
		</view>
	</view>
</template>

<script>
	export default{ 
		props: {
			avatarType:{
				type: Number,
				default: 1
			}
		},
		computed: {
			btnColor() {
				const { avatarType } = this;
				const color = avatarType === 5 ? ['bg-white'] : '';
				return color;
			}
		},
		methods: {
			handleSave() {
				this.$emit("save")
			},
			getUserInfo(result){ 
				this.$emit("getUserInfo",result)
			}
		},
	}
</script>

<style>
	button {
		z-index: 999;
	}
</style>
